import React from "react";
import { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export class Author extends Component {
  render = () => {
    return (
      <div className="tp-author-block">
        <p className="tp-author-name">{this.props.name}</p>
        <p className="tp-author-role">
          <span>Role: </span>
          <span>{this.props.role}</span>
          <FontAwesomeIcon icon={this.props.icon} title="web-ninja" />
        </p>
        <p className="tp-author-work">
          <span className="tp-author-work_until">Сделано: </span>
          <span className="tp-author-work_text">{this.props.text}</span>
        </p>
      </div>
    );
  };
}
