import React, { Component } from "react";
import { Route } from "react-router-dom";

// Components
import Developer from "./authors/Developer";
import Redactor from "./authors/Redactor";
import Writer from "./authors/Writer";
import { Default } from "./authors/Default";

export class AuthorsPage extends Component {
  upperCaseColorizer = (item) => {
    // FIND and colorize CAPS symols in a string
    if (typeof item === "string") {
      let token = item.match(/[A-ZА-Я]/);

      if (token != null) {
        // First iteration
        let offString = item.substring(token.index + 1);
        let result = [
          this.marker(item.substring(0, token.index)),
          this.marker(token[0], true),
          item.substring(token.index + 1),
        ];

        this.upperCaseColorizer(result);
      }
    } else {
      let string = item.pop();
      let token = string.match(/[A-ZА-Я]/);

      if (token != null) {
        let result = [
          ...item,
          this.marker(string.substring(0, token.index)),
          this.marker(token[0], true),
          string.substring(token.index + 1),
        ];

        this.upperCaseColorizer(result);
      } else {
        this.name = [...item, this.marker(string)];
      }
    }

    return this.name;
  };

  marker = (symbol, mark = false) => {
    // Colorize symol to red color
    if (mark) {
      return <span className="tp-upper-case">{symbol}</span>;
    } else {
      return <span>{symbol}</span>;
    }
  };

  colorizeNameCapsLetters = (name) => {
    return this.upperCaseColorizer(String(name));
  };

  render() {
    return (
      <React.StrictMode>
        <Route
          exact
          path={`/${this.props.root.location.authors}/id=1`}
          component={Developer}
          render={(props) => (
            <Developer colorizeName={this.colorizeNameCapsLetters} />
          )}
        />
        <Route
          exact
          path={`/${this.props.root.location.authors}/id=2`}
          component={Redactor}
          render={(props) => (
            <Redactor colorizeName={this.colorizeNameCapsLetters} />
          )}
        />
        <Route
          exact
          path={`/${this.props.root.location.authors}/id=3`}
          component={Writer}
          render={(props) => (
            <Writer colorizeName={this.colorizeNameCapsLetters} />
          )}
        />
        <Route
          exact
          path={`/${this.props.root.location.authors}`}
          render={(props) => <Default {...props} />}
          render={(props) => (
            <Default
              {...{
                ...props,
                colorizeNameCapsLetters: this.colorizeNameCapsLetters,
              }}
            />
          )}
        />
      </React.StrictMode>
    );
  }
}
